import React, {useContext} from "react";
import {useSelector, useDispatch} from "react-redux";

import "./ShoppingCart.scss";
import CartItem from "./CartItem";
import {Button} from "@material-ui/core";
import {Clear} from "@material-ui/icons";
import IconButton from "@material-ui/core/IconButton";
import {useHistory} from "react-router-dom";
import {ActionTypes} from "./shoppingCartReducer";
import {callEndpoint} from "../../APIUtils";
import {AppConfig} from "../../App";
import {Alert, AlertTitle} from '@material-ui/lab';

const ShoppingCart = ({ close }) => {

  const history = useHistory();
  const { endpoints } = useContext(AppConfig);
  const dispatch = useDispatch();

  const basketItems = useSelector(store => store.cart.basket);
  const total = useSelector(store => store.cart.total);
  const itemCount = useSelector(store => store.cart.itemCount);
  const status = useSelector(store => store.cart.inventoryStatus);
  const message = useSelector(store => store.cart.message);

  const validateInventory = () => {
  
    const payload = {
      basket: basketItems.map(basketItem => ({
        id: basketItem.id, title: basketItem.title, quantity: basketItem.quantity
      }))
    }
    dispatch({
      type: ActionTypes.VALIDATE_INVENTORY
    });
  
    return dispatch => {
      callEndpoint(endpoints.inventory, { body: payload }, "POST")
          .then(data => {
            if(data.statusCode === 400) {
              console.log("error", data);
              dispatch({
                type: ActionTypes.VALIDATE_INVENTORY_FAIL,
                payload: data
              });  
            }
            else {
              dispatch({
                type: ActionTypes.VALIDATE_INVENTORY_SUCCESS,
                payload: data
              });
              close();
              history.push("payment");
            }
          })
          .catch(error => {
            dispatch({
              type: ActionTypes.VALIDATE_INVENTORY_FAIL,
              payload: error
            });
          });
    }
  }

  const checkout = () => {
    dispatch(validateInventory());
    //close();
    //history.push("payment");
  }

  return (
      <div className="shopping-cart">
        <div className="header">
          <IconButton onClick={close}>
            <Clear/>
          </IconButton>
        </div>

        {basketItems.length > 0 ? (
            <div>
              <div className="basket">
                {basketItems.map((basketItem, index) =>
                    <CartItem key={index} basketItem={basketItem}/>)}
              </div>
              <div className="total-row">
                <span>Total</span>
                <span className="total">${total}</span>
              </div>
            </div>
        ) : (
            <div className="empty-basket">
              Your shopping cart is empty
            </div>
        )}

        { status === "error" || status === "failed" ? (
                <Alert severity="error" className="error">
                  <AlertTitle>{message}</AlertTitle>
                </Alert>
            ) : null}

        <div className="actions">
          <Button className="checkout-button" disabled={itemCount < 1}
                  variant="contained" color="primary" size="medium" onClick={checkout }>
            Checkout
          </Button>
        </div>
      </div>

  );
}

export default ShoppingCart;
